.App {
  text-align: center;
}

form {
  height: 625px;
  width: 400px;
  background-color: rgba(23,162,184,255);
  margin: 35px auto;
  padding: 20px 10px;
  color: white;
  font-size: 18px;
  font-family: 'Antic';
  text-align: center;
}


.controls {
  width: 90%;
  background: white;
  padding: 8px;
  border-radius: 5px;
  margin: 5px;
  font-family: Antic;
  border-color: white;
  color: black;
}

.placeholder {
  color: black;
}

.boton {
  width: 70%;
}

.boton:hover {
  width: 70%;
  background-color: #666;
  border-color: #333; 
}

h1{
  letter-spacing: 2px;
}